import React from 'react';
import { Link } from 'react-router-dom';
import styles from './dashboard.module.css';

const Dashboard: React.FC = () => {
  return (
    <div className={styles.dashboardContainer}>
      <div className={styles.dashboardHeader}>
        <h1 className={styles.dashboardTitle}>    </h1>
        <p>       .</p>
      </div>
      <div className={styles.dashboardContent}>
        <div className={styles.dashboardCard}>
          <h2 className={styles.cardTitle}></h2>
          <p className={styles.cardContent}>   .</p>
          <Link to="/articles" className={styles.cardLink}>  </Link>
        </div>
        <div className={styles.dashboardCard}>
          <h2 className={styles.cardTitle}></h2>
          <p className={styles.cardContent}>    .</p>
          <Link to="/profile" className={styles.cardLink}> </Link>
        </div>
        <div className={styles.dashboardCard}>
          <h2 className={styles.cardTitle}></h2>
          <p className={styles.cardContent}>  .</p>
          <Link to="/favorites" className={styles.cardLink}> </Link>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
