import React from 'react';
import { useFormik } from 'formik';
import Button from '../button/Button';
import styles from './registration.module.css';
import { registerUser } from 'features/auth/authAction';
import { useAppDispatch } from 'redux/hooks';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';


export interface IRegisterFormValues {
    username: string
    email: string
    password: string
  }

export default function Registration() {

  const dispatch = useAppDispatch()
  const navigate = useNavigate()

  const schema = Yup.object().shape({
    username: Yup.string()
      .required('  ')
      .min(2, ' 2 ')
      .max(15, ' 15 '),
      email: Yup.string()
      .required(' email')
      .email('   email'),
    password: Yup.string()
      .required(' ')
      .min(6, '    6 '),
  });
  const formik = useFormik({
    initialValues: {
      username: '',
      email: '',
      password: ''
    } as IRegisterFormValues,
        validationSchema: schema,
        validateOnChange: false,
    onSubmit: (values: IRegisterFormValues, {resetForm}) => {
        dispatch(registerUser(values))
        .then(()=> {
          navigate ('/')
         resetForm()
        })
        .catch((error) => {
          console.error(' :', error);
        });
   
    }
  });



  return (
    <form onSubmit={formik.handleSubmit} className={styles.signupForm}>
      <input
        type="text"
        name="username"
        placeholder=" "
        value={formik.values.username}
        onChange={formik.handleChange}
        className={formik.touched.username && formik.errors.username ? styles.inputError : ''}

      />
        {formik.touched.username && formik.errors.username && (
          <div className={styles.error}>{formik.errors.username}</div>
        )}
      <input
        type="email"
        name="email"
        placeholder="Email"
        value={formik.values.email}
        onChange={formik.handleChange}
        className={formik.touched.email && formik.errors.email ? styles.inputError : ''}

      />
      {formik.touched.email && formik.errors.email && (
          <div className={styles.error}>{formik.errors.email}</div>
        )}
      <input
        type="password"
        name="password"
        placeholder=""
        value={formik.values.password}
        onChange={formik.handleChange}
        className={formik.touched.password && formik.errors.password ? styles.inputError : ''}

      />
      {formik.touched.password && formik.errors.password && (
          <div className={styles.error}>{formik.errors.password}</div>
        )}

      <Button type="submit" name="" />
    </form>
  );
}
